.topbarContainer{
    height: 50px;
    width: 100%;
    background-color: #1877f2;
    display: flex;
    position:sticky;
    align-items: center;
    top: 0;
    z-index: 5;
}

.topbarLeft{
    flex:3;
}

.topbarMid{
    flex:5;
}

.logo{
    font-size: 24px;
    margin-left: 25px;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
}

.searchbar{
    width: 100%;
    height: 30px;
    background-color: #fff;
    border-radius:30px;
    align-items: center;
    display: flex;
}

.searchIcon{
    margin-left: 10px;
}

.searchInput{
    border:none;
    width: 70%;
}

.searchInput:focus{
    outline:none; 
}

.topbarRight{
    flex:4;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: #fff;
}   

.topbarLink{
    margin-right: 10px;
    font-size: 14px;
    cursor: pointer;
}

.topbarIcons{
    display: flex;
}

.topbarIconItem{
    margin-right: 15px;
    cursor: pointer;
    position: relative;
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius:50%;
    color: white;
    position: absolute;
    top:-5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}

.topbarImage{
    height: 32px;
    width: 32px;
    border-radius:50%;
    object-fit: cover;
    cursor: pointer;
}